import { StrictMode, useState } from "react";
import ReactDOM from "react-dom";
import React from "react";
import Client from "./Client";
import ClientForm from "./ClientForm";
import Counter from "./Counter";

const App = () => {
  const [clients, setClients] = useState([
    { id: 1, nom: "algrie" },
    { id: 2, nom: "lybie" },
    { id: 3, nom: "egypte" }
  ]);

  const handleDelete = (id) => {
    const updatedClients = [...clients];
    const index = updatedClients.findIndex((client) => client.id === id);
    updatedClients.splice(index, 1);
    setClients(updatedClients);
  };
  const handleAdd = (client) => {
    // /pour copiercl
    const updatedClients = [...clients];
    // /ajouter
    updatedClients.push({ nom: client.nom });
    // mis a jour (remplacement)
    setClients(updatedClients);
  };

  const title = "liste de pays membre";

  return (
    <div>
      <h1> {title} </h1>
      <Counter />
      <ul>
        {clients.map((client) => (
          <Client key={client.id} details={client} onDelete={handleDelete} />
        ))}
      </ul>
      <ClientForm onClientAdd={handleAdd} />
    </div>
  );
};

const rootElement = document.getElementById("root");
ReactDOM.render(
  <StrictMode>
    <App />
  </StrictMode>,
  rootElement
);
